import Container from "../components/container"
import * as React from "react"
import Header from "../components/header"
import { Link } from "gatsby"
import Img from "gatsby-image"
import Footer from "../components/footer"

export default function Home({data}) {
  return (
    <Container>
      <Header headerTitle="Homepage" headerDescription="Italian based web developer passionate about photography"></Header>
      <section className="section">
        <div className="container">
        {
          data.allFile.edges.map( 
            ({node}) => (
              <div className="block" key={node.childImageSharp.id}>
                <Img fluid={node.childImageSharp.fluid} />
              </div>
            )
          )
        }
        </div>
      </section>
      <section id="cp_categories" className="section">
        <div className="container">
          <div className="content">
            <p className="title is-4">Recent collections</p>
            <div className="columns">
              {
                data.allMarkdownRemark.edges.map(
                  ({node}) => (
                    <div className="column cp_category_element" key={node.id}>
                      <div className="card">
                        <div className="card-image">
                          <Img fluid={node.frontmatter.featuredImage.childImageSharp.fluid} />
                        </div>
                        <div className="card-content">
                          <div className="content">
                            <p className="title is-4">{node.frontmatter.title}</p>
                            <p className="subtitle is-6">
                              <span className="is-size-6" style={{ color: `#999` }}>
                                <span className={`${node.frontmatter.type !== 'primary' ? 'hide' : ''}`} >Last update: </span>
                                <span className={`${node.frontmatter.type === 'primary' ? 'hide' : ''}`} >Published on: </span>
                                {node.frontmatter.date}
                              </span>
                            </p>
                            <Link to={node.fields.slug}>
                              View more &#8250;
                            </Link>
                          </div>
                        </div>
                      </div>
                    </div>
                  )
                )
              }
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
    </Container>
  )
}

export const query = graphql`
  {
    allMarkdownRemark(sort: {order: DESC, fields: frontmatter___date}, limit: 2, filter: {fields: {slug: {nin: "/diary-page/"}}}) {
      edges {
        node {
          frontmatter {
            title
            date
            type
            featuredImage {
              childImageSharp {
                fluid(maxWidth: 540, maxHeight: 340, quality: 100) {
                  aspectRatio
                  base64
                  originalImg
                  originalName
                  presentationHeight
                  presentationWidth
                  sizes
                  src
                  srcSet
                  srcSetWebp
                  srcWebp
                }
              }
            }
          }
          fields {
            slug
          }
          id
        }
      }
    }
    allFile(sort: {fields: birthTime, order: DESC}, filter: {relativeDirectory: {eq: "photos"}}) {
      edges {
        node {
          childImageSharp {
            id
            fluid(maxWidth: 1280, maxHeight: 853, quality: 100, fit: CONTAIN, background: "white") {
              aspectRatio
              base64
              originalImg
              originalName
              presentationHeight
              presentationWidth
              sizes
              src
              srcSet
              srcSetWebp
              srcWebp
            }
          }
        }
      }
    }
  }
`